@import './reset.css';

html {
  font-family: Gordita;
  font-size: 16px;
}

a,
a:visited {
  color: #2dbaff;
  text-decoration: none;
}

body {
  display: flex;
  justify-content: center;
}

#root {
  max-width: 1440px;
  padding: 48px 64px;

  line-height: 1.2em;
}

header {
  font-size: 24px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.75px;

  margin-bottom: 32px;

  > span {
    font-weight: 300;

    margin-left: 16px;
  }

  .__logo {
    float: right;
  }
}